import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class FromDBService {

  constructor(private http: Http) { }

  public loginUser(link:string, email:string, password:string): Observable<any> {
    const headers = new Headers();
    headers.append('Content-Type', 'application/x-www-form-urlencoded');
    const urlSearchParams = new URLSearchParams();
    urlSearchParams.append('email', email);
    urlSearchParams.append('password', password);
    const body = urlSearchParams.toString();
    return this.http.post(link, body, new RequestOptions({headers: headers}))
      .map((response: Response) => <any>response
     ).catch((error:any) => Observable.throw(error.json().error || 'Server error'));
  }

  public registerUser(link:string, postObject:Object) : Observable<any> {
    const headers = new Headers({'Content-Type': 'application/json'});
    return this.http.post(link, JSON.stringify(postObject),  new RequestOptions({headers: headers}))
      .map((response: Response) => <any>response);
  }

  public getUser(link: string,id: number): Observable<any> {
    return this.http.get(link + id)
      .map((response: Response) => <any>response.json()
     ).catch((error:any) => Observable.throw(error.json().error || 'Server error'));
  }

  public getCourses(link: string): Observable<any>  {
    return this.http.get(link).map(
      (response) => response.json()
    ).catch((error:any) => Observable.throw(error.json().error || 'Server error'));
  }

  public getSchedule(link: string): Observable<any>  {
    return this.http.get(link).map(
      (response) => response.json()
    ).catch((error:any) => Observable.throw(error.json().error || 'Server error'));
  }

  public getBooks(): Observable<any>  {
    return this.http.get("./assets/books.json").map(
      (response) => response.json()
    ).catch((error:any) => Observable.throw(error.json().error || 'Server error'));
  }

}



