import { NgModule }      from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule }   from '@angular/forms';
import { RouterModule } from '@angular/router';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { FromDBService } from './fromdb.service';
import { CursuriComponent } from './cursuri/cursuri.component';
import { MenuComponent } from './menu/menu.component';
import { BooksComponent } from './books/books.component';
import { OnlinedocsComponent } from './onlinedocs/onlinedocs.component';
import { SoftwareComponent } from './software/software.component';
import { ProjectsComponent } from './projects/projects.component';
import { SourceCodeComponent } from './source-code/source-code.component';
import { ValidateComponent } from './validate/validate.component';
import { ScheduleComponent } from './schedule/schedule.component';

@NgModule({
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot([
      {
        path: 'cursuri',
        component: CursuriComponent
      },
      {
        path: 'books',
        component: BooksComponent
      },
      {
        path: 'online-docs',
        component: OnlinedocsComponent
      },
      {
        path: 'software',
        component: SoftwareComponent
      },
      {
        path: 'projects',
        component: ProjectsComponent
      },
      {
        path: 'source-code',
        component: SourceCodeComponent
      },
      {
        path: 'schedule',
        component: ScheduleComponent
      }
      ])
  ],
  declarations: [
    AppComponent,
    LoginComponent,
    RegisterComponent,
    CursuriComponent,
    MenuComponent,
    BooksComponent,
    OnlinedocsComponent,
    SoftwareComponent,
    ProjectsComponent,
    SourceCodeComponent,
    ValidateComponent,
    ScheduleComponent
  ],
  providers: [ FromDBService ],
  bootstrap: [ AppComponent ]
})

export class AppModule {
}



