import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

  private loggedIn: boolean = false;
  private firstName: string = '';
  private lastName: string = '';
  private userID: number;
  private loginLoaded: boolean = true;
  private registered: boolean = false;

  constructor( ){ }

  public setLoginStatus = function(log: boolean): void {
    this.loggedIn=log;
  }

  public setID = function(id: number): void {
    this.userID=id;
  }

  public setFirstName(name: string) {
    this.firstName = name;
  }

  public setLastName(name: string) {
    this.lastName = name;
  }

  public loadLogin(): void {
    this.loginLoaded = true;
  }

  public loadRegister(): void {
    this.loginLoaded = false;
  }

  public setRegistered(reg: boolean): void {
    this.registered = reg;
  }

}



